import {AbstractControl} from '@angular/forms';

export interface AdaptRxErrorBody {
  [key: string]: unknown; // Some validators can provide an additional data

  message?: string;
  showOnTouched?: boolean;
  priority?: number;
}

export interface AdaptRxErrors {
  [key: string]: AdaptRxErrorBody;
}

export type AdaptRxValidatorFn = (control: AbstractControl) => AdaptRxErrors | null;
